@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat"), local("Montserrat-regular"), url("../fonts/montserratregular-400.woff2") format("woff2"), url("../fonts/montserratregular-400.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Montserrat"), local("Montserrat-medium"), url("../fonts/montserratmedium-500.woff2") format("woff2"), url("../fonts/montserratmedium-500.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Montserrat"), local("Montserrat-semibold"), url("../fonts/montserratsemibold-600.woff2") format("woff2"), url("../fonts/montserratsemibold-600.woff2") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Montserrat"), local("Montserrat-bold"), url("../fonts/montserratbold-700.woff2") format("woff2"), url("../fonts/montserratbold-700.woff2") format("woff");
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2px;
  box-sizing: border-box;
  width: 120px;
  min-height: 40px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  background-color: #7859cf;
}

.button:hover {
  background-color: #9070ec;
}

.button:focus {
  background-color: #9070ec;
}

.button:active {
  background-color: #593cac;
}

.button:disabled {
  background-color: #bdbdbd;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
}

html {
  scroll-behavior: smooth;
}

.page {
  height: 100%;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  background-color: #ffffff;
  margin: 0 auto;
  width: 100%;
}

.page-header {
  position: relative;
}

.page-main {
  flex-grow: 1;
}

.page-header__logo {
  margin-right: auto;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .page-header__logo {
    margin-right: 0;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    margin-left: 39px;
  }
}

.page-header__logo-icon--tablet, .page-header__logo-icon--desktop {
  display: none;
}
@media (min-width: 768px) {
  .page-header__logo-icon--tablet {
    display: block;
  }
  .page-header__logo-icon--mobile {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header__logo-icon--tablet {
    display: none;
  }
  .page-header__logo-icon--desktop {
    display: block;
  }
}

.page-header__logo-link {
  display: block;
}
@media (min-width: 1440px) {
  .page-header__logo-link {
    display: flex;
    align-items: center;
  }
}

.page-header__logo-text {
  display: none;
}
@media (min-width: 1440px) {
  .page-header__logo-text {
    display: inline;
    margin-left: 20px;
    width: 150px;
    font-size: 12px;
    line-height: 16px;
  }
}

.navigation__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex-direction: column;
  top: 60px;
  left: 0;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .navigation__list--closed {
    display: none;
  }
}
@media (max-width: 767px) {
  .navigation__list--opened {
    position: absolute;
    display: flex;
    z-index: 2;
  }
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: auto;
    margin-left: 68px;
    margin-right: 61px;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    margin-left: 140px;
    margin-right: auto;
  }
}

.page-header__nav {
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  align-items: center;
  min-height: 60px;
  box-sizing: border-box;
  width: 320px;
}
@media (min-width: 768px) {
  .page-header__nav {
    width: 768px;
    min-height: 80px;
  }
}
@media (min-width: 1440px) {
  .page-header__nav {
    width: 1440px;
  }
}

.navigation__list--no-js {
  display: flex;
  order: 4;
  width: 100%;
}

.site-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  height: 60px;
  width: 100%;
  border-bottom: 1px solid #f0ebff;
}
.site-list__item:first-of-type {
  border-top: 1px solid #f0ebff;
}
@media (min-width: 768px) {
  .site-list__item:first-of-type {
    border: none;
  }
}
@media (min-width: 768px) {
  .site-list__item {
    width: max-content;
    height: 80px;
    border: none;
    margin-right: 2px;
  }
  .site-list__item:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 1440px) {
  .site-list__item {
    margin-right: 23px;
  }
  .site-list__item:last-of-type {
    margin-right: 0;
  }
}

.site-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #333333;
  height: 100%;
  width: 100%;
  text-decoration: none;
}
.site-list__link:not(.site-list__link--current):active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .site-list__link {
    padding: 32px 20px;
    box-sizing: border-box;
  }
  .site-list__link--current {
    position: relative;
    color: #7859cf;
  }
  .site-list__link--current::after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #7859cf;
    bottom: 0;
  }
  .site-list__link:not(.site-list__link--current):hover {
    color: #7859cf;
  }
  .site-list__link:not(.site-list__link--current):active {
    color: #7859cf;
  }
}

.site-list__icon {
  display: none;
  fill: #333333;
}

.navigation__button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  width: 60px;
  height: 60px;
}
.navigation__button--no-js {
  display: none;
}
.navigation__button--open .site-list__icon--open {
  display: block;
}
.navigation__button--close .site-list__icon--close {
  display: block;
}
.navigation__button:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .navigation__button {
    display: none;
  }
}

.nav-user {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .nav-user {
    margin-right: auto;
  }
}
@media (min-width: 1440px) {
  .nav-user {
    margin-right: 40px;
  }
}

.user__link {
  color: #333333;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
}
.user__link:hover, .user__link:focus {
  color: #7859cf;
}
.user__link:hover .user__icon, .user__link:focus .user__icon {
  fill: #7859cf;
}
.user__link:active {
  background-color: #f0ebff;
  color: #7859cf;
}
.user__link:active .user__icon {
  fill: #7859cf;
}
@media (min-width: 768px) {
  .user__link:active {
    background-color: #f0ebff;
  }
}
@media (min-width: 1440px) {
  .user__link {
    width: 100%;
    box-sizing: border-box;
    height: 80px;
    padding: 32px 20px;
    margin-right: 30px;
  }
}

.user__icon {
  fill: #333333;
}
@media (min-width: 1440px) {
  .user__icon {
    margin-right: 8px;
  }
}

.user__button {
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 60px;
  height: 60px;
}
.user__button:hover, .user__button:focus {
  color: #7859cf;
}
.user__button:hover .user__icon, .user__button:focus .user__icon {
  fill: #7859cf;
}
.user__button:active {
  background-color: #f0ebff;
  color: #7859cf;
}
.user__button:active .user__icon {
  fill: #7859cf;
}
@media (min-width: 768px) {
  .user__button:active {
    background-color: #f0ebff;
  }
}
@media (min-width: 1440px) {
  .user__button {
    height: 80px;
    width: 100%;
    padding: 32px 20px;
  }
}

.user__text {
  display: none;
}
@media (min-width: 1440px) {
  .user__text {
    display: inline;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
  }
}

.slider {
  position: relative;
  overflow: hidden;
}

.slider__slide-wrapper {
  text-align: center;
  padding: 20px 20px 60px;
}
@media (min-width: 768px) {
  .slider__slide-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-top: 60px;
    padding-bottom: 496px;
  }
  .slider__slide-wrapper::after {
    content: "";
    position: absolute;
    display: block;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 188px;
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .slider__slide-wrapper {
    flex-direction: row;
    text-align: left;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 84px;
  }
  .slider__slide-wrapper::after {
    display: none;
  }
}
.slider__slide-wrapper--flat {
  background-color: #f3ebe1;
}
.slider__slide-wrapper--lavender {
  background-color: #eae6fc;
}
.slider__slide-wrapper--espresso {
  background-color: #e5e6e8;
}

.slider__picture {
  display: block;
  margin: 0 auto 10px;
  z-index: 2;
}
@media (min-width: 768px) {
  .slider__picture {
    width: 476px;
    height: 476px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    margin: 0;
    position: absolute;
  }
}
@media (min-width: 1440px) {
  .slider__picture {
    order: 1;
    width: 600px;
    height: 600px;
    transform: none;
    position: static;
    margin-right: 30px;
  }
}

@media (min-width: 1440px) {
  .slider__description {
    padding-bottom: 7px;
    margin-right: 20px;
  }
}

.slider__badge {
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: #7859cf;
  text-transform: uppercase;
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .slider__badge {
    display: block;
    margin-bottom: 4px;
  }
}
@media (min-width: 1440px) {
  .slider__badge {
    grid-column: 2/-1;
    grid-row: 1/2;
  }
}

.slider__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 48px;
  margin-top: 0;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .slider__title {
    font-size: 56px;
    line-height: 80px;
    margin-bottom: 16px;
    letter-spacing: 0.02em;
  }
}
@media (min-width: 1440px) {
  .slider__title {
    grid-column: 2/-1;
    grid-row: 2/3;
  }
}

.slider__text {
  margin-top: 0;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .slider__text {
    width: 600px;
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .slider__text {
    grid-column: 2/-1;
    grid-row: 3/4;
    width: 580px;
    margin-bottom: 40px;
  }
}

.slider__break {
  display: none;
}
@media (min-width: 768px) {
  .slider__break {
    display: inline;
  }
}

.slider__buy {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .slider__buy {
    margin-bottom: 0;
    justify-content: center;
  }
}
@media (min-width: 1440px) {
  .slider__buy {
    grid-column: 2/-1;
    grid-row: 4/5;
    justify-content: start;
  }
}

.slider__old-price {
  color: #7859cf;
  font-size: 16px;
  line-height: 32px;
  text-decoration: line-through;
  margin: 0 9px 0 25px;
}
@media (min-width: 768px) {
  .slider__old-price {
    margin: 0 10px 0 0;
  }
}

.slider__price {
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  margin: 0 25px 0 0;
}

.slider__buy-button {
  width: 113px;
}

@media (max-width: 1439px) {
  .slider__pagination {
    display: none;
  }
}
@media (min-width: 1440px) {
  .slider__pagination {
    position: absolute;
    width: 100%;
    bottom: 50px;
    transform: translateY(-50%);
    z-index: 1;
    display: flex;
    justify-content: center;
  }
}

.slider__pagination-bullet {
  display: block;
  width: 62px;
  height: 24px;
  position: relative;
  margin: 0;
  margin-right: 8px;
}
.slider__pagination-bullet:last-of-type {
  margin-right: 0;
}
.slider__pagination-bullet::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 100%;
  height: 4px;
  background-color: #ffffff;
}
.slider__pagination-bullet--active::after {
  background-color: #7859cf;
}
.slider__pagination-bullet:not(.slider__pagination-bullet--active):hover {
  cursor: pointer;
}
.slider__pagination-bullet:not(.slider__pagination-bullet--active):hover::after {
  background-color: rgba(120, 89, 207, 0.25);
}

.slider__navigation {
  position: absolute;
  display: block;
  top: 0;
  width: 20px;
  height: 100%;
  z-index: 1;
}
.slider__navigation::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 11px;
  height: 20px;
  background-image: url("../img/icons/swiper-control.svg");
}
@media (min-width: 768px) {
  .slider__navigation::after {
    top: 39%;
  }
}
.slider__navigation--prev {
  left: 0;
}
.slider__navigation--prev::after {
  transform: translate(-50%, -50%);
}
.slider__navigation--next {
  transform: rotate(180deg);
  right: 0;
}
.slider__navigation--next::after {
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .slider__navigation--next::after {
    top: 61%;
  }
}
.slider__navigation--disabled::after {
  background-image: url("../img/icons/swiper-control-disabled.svg");
}
.slider__navigation:hover {
  cursor: pointer;
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}
@media (min-width: 768px) {
  .slider__navigation {
    width: 80px;
  }
}

.benefits {
  width: 320px;
  text-align: center;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .benefits {
    width: 768px;
    margin-top: 60px;
  }
}
@media (min-width: 1440px) {
  .benefits {
    width: 1440px;
    margin-top: 0;
    padding-top: 100px;
  }
}

.benefits__header {
  margin-bottom: 4px;
  margin-top: 50px;
  padding: 0 20px;
  color: #7859cf;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .benefits__header {
    margin-top: 0;
  }
}

.benefits__headline {
  margin-top: 0;
  margin-bottom: 36px;
  padding: 0 20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .benefits__headline {
    font-size: 42px;
    line-height: 64px;
    margin-bottom: 60px;
  }
}
@media (min-width: 1440px) {
  .benefits__headline {
    margin-bottom: 88px;
  }
}

.benefits__list {
  margin: 0 auto 110px;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .benefits__list {
    width: 708px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
    row-gap: 50px;
    margin-bottom: 100px;
  }
}
@media (min-width: 1440px) {
  .benefits__list {
    width: 1360px;
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 96px;
  }
}

.benefits__item {
  position: relative;
  padding: 66px 20px 0;
  margin-bottom: 32px;
}
.benefits__item::after {
  content: "";
  display: block;
  position: absolute;
  background-repeat: no-repeat;
  top: 20%;
  left: 50%;
  transform: translate(-55%, -54%);
}
.benefits__item--speed::after {
  width: 46px;
  height: 52px;
  background-image: url("../img/icons/speed.svg");
}
.benefits__item--comfort .benefits__text {
  width: 237px;
}
.benefits__item--comfort::after {
  width: 48px;
  height: 52px;
  background-image: url("../img/icons/comfort.svg");
}
.benefits__item--energy::after {
  width: 38px;
  height: 52px;
  background-image: url("../img/icons/energy.svg");
}
.benefits__item--eco::after {
  width: 42px;
  height: 52px;
  background-image: url("../img/icons/ecology.svg");
}
@media (min-width: 768px) and (max-width: 1440px) {
  .benefits__item::after {
    transform: none;
    top: 0;
    left: 0;
  }
}
@media (min-width: 768px) {
  .benefits__item {
    text-align: left;
    margin-bottom: 0;
    padding: 0 0 0 72px;
  }
  .benefits__item .benefits__text {
    width: auto;
    margin: 0;
  }
  .benefits__item--eco .benefits__text {
    width: 230px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .benefits__item--eco .benefits__text {
    width: auto;
  }
}
@media (min-width: 1440px) {
  .benefits__item {
    padding-top: 70px;
    padding-left: 0;
    text-align: center;
  }
  .benefits__item .benefits__text {
    margin: 0 auto;
  }
  .benefits__item::after {
    top: 11%;
  }
  .benefits__item--comfort .benefits__text {
    width: 265px;
  }
  .benefits__item--energy .benefits__text {
    width: 300px;
  }
}

.benefits__item:last-of-type {
  margin-bottom: 0;
}

.benefits__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
}
@media (min-width: 768px) {
  .benefits__title {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 11px;
  }
}

.benefits__text {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .benefits__text {
    width: 260px;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .benefits__text {
    margin: 0 auto;
    width: auto;
  }
}

.catalog {
  width: 320px;
  padding: 0 20px;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #f8f8f8;
}
@media (min-width: 768px) {
  .catalog {
    padding-top: 77px;
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .catalog {
    width: 1440px;
    padding: 119px 40px 0;
    display: grid;
    grid-template-columns: 318px 1fr;
    column-gap: 30px;
  }
}

.catalog__header {
  line-height: 16px;
  margin-top: 0;
  margin-bottom: 5px;
  color: #7859cf;
  font-weight: 700;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 1440px) {
  .catalog__header {
    grid-column: 1/-1;
  }
}

.catalog__headline {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .catalog__headline {
    font-size: 42px;
    line-height: 64px;
  }
}
@media (min-width: 1440px) {
  .catalog__headline {
    grid-column: 1/-1;
